@media ( max-width:576px )
  body
    font-size: 15px

  .button
    font-size: 20px

  .main-slider
    max-height: 553px
  .main-slider__container
    margin-left: 0
    padding-top: 208px
  .main-slider__head
    max-width: 70%
    line-height: 110%
    font-size: 36px
  .main-slider__btn
    @include hide

  .main-about
    padding-top: 50px
    padding-bottom: 60px
  .main-about__spec-wrap::before
    display: none
  .main-about__spec-icon
    width: 75px
    height: 75px
    margin-bottom: 30px
    &--chair
      width: 40px
    &--wardrobe
      width: 40px
    &--home
      width: 40px
  .main-about__spec-title
    font-size: 15px
  .main-about__spec-text
    font-size: 13px

  .mini-slider
    padding-bottom: 45px
  .mini-slider__slider
    height: 300px
  .mini-slider__intro
    width: 170px
    top: 83px
  .mini-slider__button
    padding: 6px 20px
  .mini-slider__btn
    @include hide

  .page
    padding-top: 80px
  .page-hero__wrapper
    flex-wrap: wrap
  .page-hero__head
    font-size: 100px
    width: 210px
    &--interior
      margin-right: 0
      &::before
        height: 79px
        width: 45px
        top: 4px
        right: 61px
      &::after
        right: 71px
        top: 239px
        height: 30px
        width: 30px
    &--mobilier
      margin-right: 0
      &::before
        height: 79px
        width: 45px
        top: 97px
        right: 61px
      &::after
        right: 13px
        top: 239px
        height: 30px
        width: 30px
    &--arhitectura
      margin-right: 0
      &::before
        height: 79px
        width: 45px
        top: 4px
        right: -3px
      &::after
        right: 13px
        top: 239px
        height: 30px
        width: 30px
  .page-hero__content
    padding-top: 30px
    max-width: 100%
    width: 100%
    p:not(:last-child)
      margin-bottom: 15px
  .page__photo
    &--middle,
    &--small
      height: 160px
    &--median,
    &--big
      height: 240px

  .footer__content
    grid-template-rows: 50px 1fr
  .footer__contact,
  .footer__adress
    font-size: 16px
  .footer__copyright
    font-size: 14px
