@media ( max-width:992px )
  body
    font-size: 22px
  .button
    font-size: 22px
    &::before
      display: none

  .header__container
    flex-wrap: wrap
  .header__link--phone
    order: 3
    margin-left: 30px
    margin-left: auto
  .menu
    margin-left: 30px
  .menu__item:not(:last-child)
    margin-right: 36px
  .main-slider
    max-height: 602px
  .main-slider__container
    padding-top: 250px
  .main-slider__head
    font-size: 61px
  .main-slider__slide::before
    display: none

  .main-about
    padding-top: 80px
    padding-bottom: 160px
  .main-about__head
    font-size: 48px
  .main-about__descr
    max-width: 100%
  .main-about__text:not(:last-child)
    margin-bottom: 30px
  .main-about__intro
    @include hide
  .main-about__spec
    padding-top: 80px
    justify-content: space-evenly
    flex-grow: 1
  .main-about__wrap-btn
    @include hide

  .mini-slider
    padding-top: 0
  .mini-slider__container
    position: relative
    padding-left: 0
    padding-right: 0
  .mini-slider__intro
    position: absolute
    top: 110px
    left: 8%
    width: 270px
    margin-right: 0
    z-index: 2
  .mini-slider__slider
    width: 100%
  .mini-slider__button
    margin-top: 102px
    margin-left: 8%
    padding: 10px 35px

  .page-hero__wrapper
    padding-left: 11%
  .page-hero
    min-height: 730px
  .page-hero__head
    font-size: 175px
  .page-hero__head
    &--interior
      margin-right: 45px
      &::before
        top: 32px
        right: 77px
      &::after
        right: 97px
    &--mobilier
      margin-right: 50px
      &::before
        top: 186px
        right: 94px
      &::after
        right: 10px
    &--arhitectura
      width: 358px
      margin-right: 40px
      &::before
        top: 32px
        right: 1px
      &::after
        right: 20px
        bottom: 77px

  .page-hero__btn
    padding: 10px 20px
    font-size: 16px

  .footer__content
    // align-content: space-between
    // justify-content: stretch
    // align-items: stretch
    // justify-items: stretch
    grid-template-rows: 67px 1fr
  .footer__head
    grid-column: 1 / 5
  .footer__list
    grid-column: 5 / 7
    // grid-row: 1 / 3
    grid-row: 2 span
    flex-direction: column-reverse
    justify-content: center
    // justify-self: stretch
    // align-self: stretch
    // justify-content: stretch
    // jus-items: stretch
    // justify-content: stretch
    // align-items: stretch
    margin-bottom: 0
  .footer__item
    &:not(:last-child)
      margin-right: 0
    &:not(:first-child)
      margin-bottom: 38px !important

  .footer__data
    grid-column: 1 / 5
    grid-row: 2 / 3
  .footer__contact
    width: 100%
    margin-right: 0
    margin-bottom: 28px
  .footer__adress
    width: 100%
    margin-bottom: 28px
